@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
    --c: rgba(255, 255, 255, 0.7);
    --t: transparent;
}

.field {
    position: relative;
    width: 700px;
    height: 700px;
    overflow: hidden;
    border: black 2px solid;
    margin: auto;
    border-radius: 15px;
    background-image: repeating-linear-gradient( 45deg, rgba(255, 255, 255, 0.7) 0, rgba(255, 255, 255, 0.7) 20px, transparent 20px, transparent 32px, rgba(255, 255, 255, 0.7) 32px, rgba(255, 255, 255, 0.7) 44px, transparent 44px, transparent 56px, rgba(255, 255, 255, 0.7) 56px, rgba(255, 255, 255, 0.7) 68px, transparent 68px, transparent 80px, rgba(255, 255, 255, 0.7) 0), repeating-linear-gradient( -45deg, rgba(255, 255, 255, 0.7) 0, rgba(255, 255, 255, 0.7) 20px, transparent 20px, transparent 32px, rgba(255, 255, 255, 0.7) 32px, rgba(255, 255, 255, 0.7) 44px, transparent 44px, transparent 56px, rgba(255, 255, 255, 0.7) 56px, rgba(255, 255, 255, 0.7) 68px, transparent 68px, transparent 80px, rgba(255, 255, 255, 0.7) 0), linear-gradient(to bottom right, #fc354c, #0abfbc);
    background-image: repeating-linear-gradient( 45deg, var(--c) 0, var(--c) 20px, var(--t) 20px, var(--t) 32px, var(--c) 32px, var(--c) 44px, var(--t) 44px, var(--t) 56px, var(--c) 56px, var(--c) 68px, var(--t) 68px, var(--t) 80px, var(--c) 0), repeating-linear-gradient( -45deg, var(--c) 0, var(--c) 20px, var(--t) 20px, var(--t) 32px, var(--c) 32px, var(--c) 44px, var(--t) 44px, var(--t) 56px, var(--c) 56px, var(--c) 68px, var(--t) 68px, var(--t) 80px, var(--c) 0), linear-gradient(to bottom right, #fc354c, #0abfbc);
}

.field>div {
    position: absolute;
    will-change: transform;
    height: 50px;
    width: 50px;
    top: 0;
    margin-left: 0;
    background-image: url(/moving-bug/static/media/bug.9b51008f.svg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50px 50px;
    cursor: pointer;
}

.field>div:nth-child(2) {
    height: 50px;
    width: 50px;
    background-size: 50px 50px;
    background-image: url(/moving-bug/static/media/bug1.835616ad.svg);
}

.field>div:nth-child(3) {
    height: 50px;
    width: 50px;
    background-size: 30px 30px;
    background-image: url(/moving-bug/static/media/bug2.33845da3.svg);
}

.field>div:nth-child(4) {
    height: 50px;
    width: 50px;
    background-size: 50px 50px;
    background-image: url(/moving-bug/static/media/bug3.e4ae1af1.svg);
}

.field>div:nth-child(5) {
    height: 50px;
    width: 50px;
    background-size: 50px 50px;
    background-image: url(/moving-bug/static/media/insect.daedbf72.svg);
}
.speed-control {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    border: 2px solid  black;
    border-radius: 20px;
    margin-bottom: 10px;
    background: #fff;
}

.bugs-num {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    border: 2px solid black;
    border-radius: 20px;
    margin-bottom: 10px;
    background: #fff;
}

.direction-control {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    border: 2px solid black;
    border-radius: 20px;
    margin-bottom: 10px;
    background: #fff;
    height: 2rem;
}
.curve-controls {
    background: #fff;
    border-radius: 15px;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
}

legend {
    background: rgb(38, 108, 219);
    border-radius: 10px;
    padding: 5px;
    border: 1px solid black;
    color: #fff;
}

.function-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.select {
    width: 150px;
}
*,
html {
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
}

h1 {
    font-size: 1.2rem;
    text-align: center;
    padding: 15px;
    margin: 0;
}

.app {
    display: flex;
    height: 100vh;
}

.content {
    flex: 1 1 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient( 90deg, rgba(255, 225, 132, .7) 50%, rgba(255, 255, 255, 0) 50%), linear-gradient( 90deg, rgba(32, 113, 120, .3) 50%, rgba(255, 255, 255, 0) 50%), linear-gradient( 90deg, rgba(255, 150, 102, .3) 50%, rgba(255, 255, 255, 0) 50%), linear-gradient( 90deg, rgba(23, 76, 79, .1) 50%, rgba(255, 255, 255, 0) 50%);
    background-size: 7em 7em, 5em 5em, 3em 3em, 1em 1em;
    background-color: #F5E9BE;
}

.sidebar {
    flex: 1 1 30%;
    padding: 25px;
    border-left: 3px solid black;
    background: #DAB420;
}

.btn {
    font-size: 2rem;
    background: none;
    border: none;
    cursor: pointer;
}

.btn:disabled {
    cursor: not-allowed;
}

.button-group {
    display: flex;
    align-items: center;
    min-width: 100px;
    justify-content: space-between;
}

input[type=checkbox] {
    height: 0;
    width: 0;
    visibility: hidden;
}

.checkbox {
    cursor: pointer;
    width: 50px;
    height: 25px;
    background: grey;
    display: block;
    border-radius: 10px;
    position: relative;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
}

.checkbox:after {
    content: '';
    position: absolute;
    top: 0px;
    left: 0px;
    width: 25px;
    height: 25px;
    background: rgb(212, 43, 85);
    border-radius: 25px;
    transition: 0.3s;
}

input:checked+.checkbox {
    background: #bada55;
}

input:checked+.checkbox:after {
    left: 100%;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.checkbox:active:after {
    width: 40px;
}
